"use strict";

const func = {};


/**
 *     
 * @param {Array} arr   	
 * @param {String} key    
 * @param {String} value   
 * @param {Function} step      
 * @param {Function} final    
 * @return {Object}      
 */
func.ifExist = function(arr, key, value, step, final) {
	let arrLength = arr.length;
	let result = {
		index : null,
		object : {},
		status : false
	}
	for (var i = 0; i < arrLength; i++) {
		if (arr[i][key] == [value]) {
			result.index = i;
			result.object = arr[i];
			result.status = true;
			if (step) step(result);
		}
	}
	if (final) final(result);
	return result; 
}

/**
 *         
 * @param {Array} arr   
 * @param {String} key  
 * @param {Function} step      
 * @param {Function} final    
 * @return {Object}         
 */
func.joinObjectsKeys = function(arr, key, step, final){
	let length = arr.length;
	let tempList = [];
	let result = {};
	for(var i = 0; i < length; i++ ){
		tempList.push(arr[i][key]);
		if (step) step({
			object: arr[i],
			value: arr[i][key],
			index: i
		});
	}
	result.array = tempList;
	result.string = tempList.join();
	if (final) final(result)
	return result
}

/**
 *  
 * @param {Array} arr  
 * @param {Function} step           
 */
func.each = function(arr,step){
	let length = arr.length;
	for(var i = 0; i < length; i++){
		step({
			index: i,
			elem: arr[i]
		});
	}
}


export default func;


